import React from 'react'
import { Link, NavLink } from 'react-router-dom';


const TopNav = () => {
    return (
        <div className="topnav">
            <img src="https://s4.uupload.ir/files/poetry_g0fs.png" />
            <Link to="/"></Link>
            <Link to="/fortune"> </Link>
            <Link to="/poets"> </Link>
            <Link to="/random"> </Link>
            <button href="#signin" style={{ textAlign: 'left', justifyContent: "right" }}></button>
        </div>
    );
}

export default TopNav;